const smsText = (textRef) =>{
    if(textRef.current){
      const rawHTML = textRef.current.innerHTML;
      const convertedText = rawHTML
      .replace(/<\/?(span|div|hr)[^>]*>/gi, "") // Matches opening or closing <span> <hr> and <div> tags
      .replace(/\.\s*\./g, "") // Remove consecutive dots if any
      .replace(/<br\s*\/?>/gi, "\n")
      .replace(/&nbsp;/g, "");
      //setRawText(convertedText);
      return convertedText;
    }
  }

