
import generateTimeOptions from '../components/time-select';
import { useState } from "react";
import { useSettings } from '../context/SettingsContext';
import { Button } from '@mui/material';
import { time12 } from "../components/total-hours";
import { time24 } from "../components/total-hours";




export default function Settings({}) {

 
  const  {settings, updateSettings}  = useSettings();

      const [ot,setOt] = useState(true);
    const timeOptions = generateTimeOptions(settings.timeFormat,settings.timeIncrement?30:15);

    return (
      <div className="page" >
       
        <div className="settings-content">
        <label >
            
          
            <input type="checkbox" 
            // checked={ampm}
            checked={settings.timeFormat} 
            //onChange={()=>setAmpm(!ampm)}
             onChange={(e)=>{
              updateSettings({timeFormat: e.target.checked});
              updateSettings({startTime: time12(settings.startTime)});
              updateSettings({endTime: time12(settings.endTime)});
            
            }}

            /> 12 Hour (AM/PM)
            <br />
            <input type="checkbox"
            checked={!settings.timeFormat}
            //onChange={()=>setAmpm(!ampm)}
            onChange={(e)=>{
              updateSettings({timeFormat: !e.target.checked});
              updateSettings({startTime: time24(settings.startTime)});
              updateSettings({endTime: time24(settings.endTime)});
            
            }}


            /> 24 Hour (Military)
            </label>
            <br />
            
            <label>
            
            <hr></hr>
            <input type="checkbox"
             //checked={increment} 
             //onChange={()=>setIncrement(!increment)}
             checked={settings.timeIncrement}
             onChange={(e)=>updateSettings({
              timeIncrement: e.target.checked
            })}
             
               />
                30 Minute
            <br />
            <input type="checkbox" 
            checked={!settings.timeIncrement}
            onChange={(e)=>updateSettings({
             timeIncrement: !e.target.checked
            })}
            
            /> 15 Minute
            </label>
  
            <br />
            <label>
            
            <hr />
            <select  className="cool-time-select"// 
        style={{ marginRight: "0px",  minWidth:"100px", maxHeight:"30px"}} 
        value={settings.startTime}
        onChange={(e)=>updateSettings({
          startTime: e.target.value
        })} >
         {timeOptions.map((time, indx) => (
        <option  key={indx} value={time}>
          {time}
        </option>
      ))}
        </select>         Start 
            <br />
            <select  className="cool-time-select"// 
        style={{ marginRight: "0px",  minWidth:"100px", height:"30px"}} 
        value={settings.endTime}
        onChange={(e)=>updateSettings({
          endTime: e.target.value
         })}
        >
         {timeOptions.map((time, indx) => (
        <option  key={indx} value={time}>
          {time}
        </option>
      ))}
        </select>  End
       
            </label>


        
      {/*}   
          <label>
            Over Time policy
            <hr />
            <input type="checkbox" 
                   checked={ot} 
                   onChange={()=>setOt(!ot)}
       /> OT after 8 Hrs/day
            <br />
            <input type="checkbox"
                   checked={!ot} 
                   onChange={()=>setOt(!ot)}
        /> OT after 40 Hrs/Week
          </label>
          */}
          <br />
         
          
          
        </div>
      </div>
    );
  }