import React,{useEffect, useState} from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import getMonth from "./date-header";
import {getStartDay} from "./date-header";
import smsText from "./sms-text";
//import plainText from "./raw-html";
import { useNavigate } from 'react-router-dom';
import Settings from "../settings/Settings";
import { useSettings } from "../context/SettingsContext";
import UseMondayUpdate from "../hooks/use-monday.js"
import { useMondayUpdate1 } from "../hooks/use-monday.js";


const TopMenu = (props) => {
  
  const  {settings, updateSettings}  = useSettings();
  
  const navigate = useNavigate();

  const handleWeekChange = (chosenWeek) => 
    {
      if(chosenWeek==="this week"){
      props.setChangeWeek(1)
    } 
    else if(chosenWeek==="last week"){
      props.setChangeWeek(-6);
    }
    else if(chosenWeek==="week before last"){
      props.setChangeWeek(-13);
    }
 
    };

    useEffect(()=>{props.setRawText(smsText(props.textRef));

      
      const savedData = localStorage.getItem(getStartDay(props.changeWeek));
      if(savedData){props.setWorkHours(JSON.parse(savedData));}
      
    


    }, [props.changeWeek]); //update sms after week change


  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleSave = () => {
    setAnchorEl(null);
    localStorage.setItem(getStartDay(props.changeWeek), JSON.stringify(props.workHours));


  }
  
  const handleOpen = () => {
    setAnchorEl(null);
    const savedData = localStorage.getItem(getStartDay(props.changeWeek));
    if(savedData){props.setWorkHours(JSON.parse(savedData));}
 }
 const handleRevert = () => {
  setAnchorEl(null);
  const savedData = localStorage.getItem("workHours");
  if(savedData){props.setWorkHours(JSON.parse(savedData));}
}



  const clearJobs =()=>{

   //props.setJobNames([]);
   props.setJobOptions([{ value: "New Item", label: "🔵  New Job  " },]);
   
  }

  const resetDefaults =()=>{
    const updatedWorkHours = [...props.workHours];
    updatedWorkHours.map((day)=>{
      if(day.isChecked){
        day.tasks[0].start = settings.startTime;
      }
      else{
        day.tasks[0].start = settings.endTime;
        day.tasks[0].storedStart = settings.startTime;
      }
      //day.isChecked?day.start = settings.startTime:day.start = settings.endTime;
      day.tasks[0].end = settings.endTime;
      day.tasks[0].job = props.jobOptions[0].label;
    })
    props.setWorkHours(updatedWorkHours);

  }

  const share =()=>{
    const selectedHref = `sms:?&body=${encodeURIComponent(props.rawText)}` // Get the URL from the option value
    if (selectedHref) {
      window.location.href = selectedHref; // Navigate to the selected URL
    }

  }

  return (
    
    <AppBar position="static" style={{ backgroundColor: "#333" }}>
      
      <Toolbar>
        <Typography variant="h5" style={{ flexGrow: 1 }}>
        &nbsp;Weekly Time Card
        <br />

          <select 
       style={{marginBottom:"0px",width:"85%"}}
       className="cool-date-header"
       onChange={(e) => handleWeekChange(e.target.value)}
      >
        <option value="this week">
        {getMonth(1)} 
        </option>
        <option value="last week">
        {getMonth(-6)}
        </option>
       
        <option value="week before last">
        {getMonth(-13)}
        </option>
      </select>
     
        </Typography>

        <Button
           //variant="contained"
           sx={{ 
             fontSize: '23px',  // or '1.5rem' or 'large'
            // color: "inherit"
           }}
          color="inherit"
          onClick={()=>share()}
        >
          ⬆
        </Button>
        
       

       
        <Button
           sx={{ 
             fontSize: '30px',  // or '1.5rem' or 'large'
            // color: "inherit"
           }}
          color="inherit"
          //onClick={()=>navigate('/settings')}
          onClick={(e)=>setAnchorEl(e.currentTarget)}
        >
          ...
        </Button>

        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={()=>setAnchorEl(null)}
        >
         {/*} <MenuItem onClick={()=>{setAnchorEl(null);share(); }}>Share</MenuItem>
          <MenuItem onClick={() => {setAnchorEl(null);navigate('/settings');}}> Settings</MenuItem>
           */}
          <Settings />
          <MenuItem onClick={()=>{setAnchorEl(null);resetDefaults();}}>Set This Week To Defaults</MenuItem>
          <MenuItem onClick={()=>{handleSave();}}></MenuItem>
          <MenuItem onClick={()=>{handleOpen();}}></MenuItem>
          <MenuItem onClick={()=>{handleRevert();}}></MenuItem>
          </Menu>
      </Toolbar>
     
    </AppBar>
 
  );
};

export default TopMenu;
