

export const formatTime12 = (hours,minutes) =>{
  const period = hours >= 12 ? "PM" : "AM";
  const formattedHours = hours % 12 || 12; // Convert 24-hour time to 12-hour time
  const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
  const formattedTime = `${formattedHours}:${formattedMinutes} ${period}`;
  return formattedTime;

}

const generateTimeOptions = (format,increment) => {
    
    const times = [];
    const start = new Date();
    start.setHours(0, 0, 0, 0); // Set to 12:00 AM
    const end = new Date();
    end.setHours(23, 30, 0, 0); // Set to 11:30 PM
  
    while (start <= end) {
      const hours = start.getHours();
      const minutes = start.getMinutes();

      if(format){
        const formattedTime = formatTime12(hours,minutes)
        /*
         const period = hours >= 12 ? "PM" : "AM";
        const formattedHours = hours % 12 || 12; // Convert 24-hour time to 12-hour time
        const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
        times.push(`${formattedHours}:${formattedMinutes} ${period}`);
        */
       times.push(formattedTime);
      start.setMinutes(start.getMinutes() + increment); // Increment by 30 minutes
      }

      else{
        const formattedHours = hours;
        const period = "";
        const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
       // times.push(`${formattedHours}:${formattedMinutes} ${period}`);
        times.push(`${formattedHours}:${formattedMinutes}`);
        start.setMinutes(start.getMinutes() + increment); // Increment by 30 minutes
      }
      //const formattedHours = hours;
  
    }
  
    return times;
  };

  export default generateTimeOptions;