
import getMonth from "./date-header";
import calculateTotalHours from "./total-hours";
import weekTotal from "./week-total";
import getDatesOfCurrentWeek from "./current-week";



 



const plainText = (workHours,changeWeek) => {

  const addUnderline = (text) => {
    // Unicode combining low line character
    const UNDERLINE = '\u0332';
    
    // Add combining character after each letter
    return text.split('').map(char => char + UNDERLINE).join('');
  };

    const daysOfWeek = getDatesOfCurrentWeek(changeWeek);  //chgwk is 1 for this week and -6 for last
 

    const [reg,ot] = weekTotal(workHours);

    return(

<div  className="blue"  >  
      <hr />
        
        {(getMonth(changeWeek))}      {/*  date range header     */}
      <br /><hr />
          <span>{reg} Hours  <br />{ot} OverTime </span>
        <br />=============<br />
        {daysOfWeek.map((day, index) => (
        <div   key={index}  >
          {workHours[index].isChecked ? (// only show hours for checked days
        <><span style={{ marginLeft:"0px"}}>{daysOfWeek[index]}<br /> {workHours[index].tasks[0].job}</span>
         
          {/*   job name    */}
          <br />
             <span  className="indent">{workHours[index].tasks[0].start}-{workHours[index].tasks[0].end} <br />
            {index<5?(
                <>
        
                {calculateTotalHours(workHours[index].tasks[0].start, workHours[index].tasks[0].end)>8?8:calculateTotalHours(workHours[index].tasks[0].start, workHours[index].tasks[0].end).toFixed(2)-1+1} HR
                &nbsp;&nbsp;{calculateTotalHours(workHours[index].tasks[0].start, workHours[index].tasks[0].end)>8?(calculateTotalHours(workHours[index].tasks[0].start, workHours[index].tasks[0].end).toFixed(2)-8).toFixed(2)-1+1:0} OT
           
              </>
               ) : (
                
                <>
                0.0 Hrs  &nbsp; &nbsp;{calculateTotalHours(workHours[index].tasks[0].start, workHours[index].tasks[0].end).toFixed(1)} OT
                
                
                </>)}
           
           
           
            </span>
          
            <br />------------------
            <br />
            </>
              
            ) : (  // no work days
              <>
               {daysOfWeek[index] }
               <br />
            
              NO HOURS
               <br />------------------
               <br />
              </>
            )}
        
            </div>
         ))}
               
            
        
               <br />
          </div>
        
    
    )


}
export default plainText;


